import React, { FC } from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import { FieldValue, SubmitHandler, useForm, ValidateResult } from 'react-hook-form';
import { FormButton, FormContainer } from 'src/shared/ui/form';
import PasswordFormInput from '../AuthFormInputs/PasswordFormInput';
import s from 'src/features/forms/AuthFrom/ui/LoginForm/LoginForm.module.scss';
import EmailFormInput from 'src/features/forms/AuthFrom/ui/AuthFormInputs/EmailFormInput';
import { AuthPair } from 'src/shared/model/types';

type RegFormValues = {
  email: string;
  password: string;
  confirmPassword: string;
};

type RegFormProps = {
  onSuccessRegistration: (authPair: AuthPair) => void;
} & WithTranslation;

const RegForm: FC<RegFormProps> = ({ t, onSuccessRegistration }) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<RegFormValues>({
    defaultValues: { email: '', password: '', confirmPassword: '' },
  });

  const handleClick: SubmitHandler<RegFormValues> = (data) => {
    onSuccessRegistration(data);
  };

  const validateConfirmPassword = (value: FieldValue<RegFormValues>, formValues: RegFormValues): ValidateResult => {
    return (
      value === formValues['password'] ||
      t('forms.passwordMismatch', { ns: 'errors', defaultValue: '  ' })
    );
  };

  return (
    <FormContainer onSubmit={handleSubmit(handleClick)} title={t('registration.title', '')}>
      <EmailFormInput
        label={t('registration.inputs.email.label', 'E-mail')}
        error={errors.email?.message}
        placeholder={t('registration.inputs.email.placeholder', ' e-mail')}
        register={register}
        t={t}
      />

      <PasswordFormInput
        label={t('registration.inputs.password.label', '')}
        error={errors.password?.message}
        placeholder={t('registration.inputs.password.placeholder', ' ')}
        name={'password'}
        register={register}
        t={t}
      />

      <PasswordFormInput
        label={t('registration.inputs.password.label', ' ')}
        error={errors.confirmPassword?.message}
        placeholder={t('registration.inputs.confirmPassword.placeholder', ' ')}
        name={'confirmPassword'}
        register={register}
        t={t}
        validatePasswordConfirm={validateConfirmPassword}
      />

      <FormButton className={s.submit_button} type={'submit'}>
        {t('registration.submit', '')}
      </FormButton>
    </FormContainer>
  );
};

export default withTranslation(['forms', 'errors'])(RegForm);
